import React, { Component } from 'react'
import { Message } from '../index'
import axios from 'axios'
class Login extends Component {
  constructor () {
    super()
    this.state = {
      key:'',
    }
  }
  componentWillMount () { // token
    if (localStorage.token) {
      this.props.history.push('/')
    }
  }
  handelChange = (e) => {
    this.setState({
      key:e.target.value
    })
  }
  handleSubmit = async () => {
    if (this.state.key === '') {
      Message.warning('token')
      return
    }
    axios.post('/accesstoken', {
      accesstoken:this.state.key
    })
      .then(res => {
        console.log(this.props)
        localStorage.token = this.state.key // token
        let { pathname } = this.props.history.location.state.from // 
        this.props.history.push(pathname) // 
      })
      .catch(err => {
        console.log(err)
        Message.error('Token')
      })
  }
  render () {
    return (
      <div className='rootBox'>
        <header className='login-header'>
          <h2></h2>
        </header>
        <div className='login-content'>
          <div className='login-content-input'>
            <input placeholder='Access Token' name='key' onChange={this.handelChange} />
          </div>
          <button onClick={this.handleSubmit}></button>
        </div>
      </div>
    )
  }
}

export default Login



