import React, { Component } from 'react'
import { Footer, Message } from '../index'
import axios from 'axios'
class NewTopic extends Component {
  constructor () {
    super()
    this.state = {
      title:'',
      tab:'',
      content:'',
    }
  }
  handleTitleChange = (event) => { // setsta,teevent.target.namename 
    this.setState({
      [event.target.name]:event.target.value
    })
  }
  handleSubmit = () => {
    const { title, tab, content } = this.state
    if (title === '' && content === '') {
      Message.error('')
      return
    }
    axios.post('/topics', {
      accesstoken: localStorage.token,
      title,
      tab,
      content
    })
      .then(res => {
        this.props.history.puhs(`/topic/${res.data.topic_id}`)
      })
      .catch(err => {
        Message.error(`${err.response.data.error_msg}`)
      })
  }
  render () {
    console.log(this.props)
    return (
      <div className='rootBox'>
        <header className='new-header' flex='flex'>
          <h2></h2><div className='submit' onClick={this.handleSubmit} />
        </header>
        <form className='new-topic' onChange={this.handleTitleChange} >
          <input autoFocus type='text' placeholder='' name='title' />
          <select className='new-topic-selece' name='tab'>
            <option value='' defaultValue ></option>
            <option value='share' ></option>
            <option value='ask'></option>
            <option value='job'></option>
            <option value='dev'></option>
          </select>
          <div className='new-topic-textarea-box'>
            <textarea placeholder='Markdown' name='content' />
          </div>
        </form>
        <Footer />
      </div>
    )
  }
}

export default NewTopic



