import React from 'react'
import Notification from 'rc-notification'
// antdmessage 
let defaultDuration = 2
let messageInstance
let key = 1
let prefixCls = 'message'
let getContainer

function getMessageInstance () { // Notification
  messageInstance = messageInstance || Notification.newInstance({ // messageInstance
    prefixCls, // 
    transitionName: 'up', // 
    style: {}, // 
    getContainer,
  })
  return messageInstance
}
/**
 * 
 * @param {String} content - 
 * @param {String} key - 
 * @param {Boolean} closable - 
 * @param {Function} onClose - 
 * @param {Number} duration - 
 * @param {Object} style - 
 * 
 * /
 
 /* 
 *antd
 * @param {String} content - React
 * @param {Number} duration - 
 * @param {String} type - 
 * @param {Function} onClose - 
 */
function notice (content, duration = defaultDuration, type, onClose) {
  let instance = getMessageInstance() // 
  instance.notice({
    key,
    duration, // 
    style: {}, // 
    content: ( // 
      <div className={`${prefixCls}-box ${prefixCls}-${type}`}>
        <span className={`${prefixCls}-content`}>
          {content}
        </span>
      </div>
    ),
    onClose,
  })
  return (function () {
    let target = key++ // target = key key++
    return function () {
      instance.removeNotice(target) // 
    }
  }())
}

export default {
  info: (content, duration, onClose) => {
    return notice(content, duration, 'info', onClose)
  },
  error: (content, duration, onClose) => {
    return notice(content, duration, 'error', onClose)
  },
  warning: (content, duration, onClose) => {
    return notice(content, duration, 'warning', onClose)
  }
}



