import React from 'react'
import { NavLink } from 'react-router-dom'

const Footer = () => {
  return (
    <footer className='AppFooter'>
      <ul className='nav'>
        <li>
          <NavLink to='/'></NavLink>
        </li>
        <li>
          <NavLink to='/newtopic'></NavLink>
        </li>
        <li>
          <NavLink to='/messages'></NavLink>
        </li>
        <li>
          <NavLink to='/userhome'></NavLink>
        </li>
      </ul>
    </footer>
  )
}

export default Footer



